import * as vscode from 'vscode';
import { Disposable } from './util/dispose';

export abstract class PreviewStatusBarEntry extends Disposable {
	private e: unknown | undefined;

	protected readonly f: vscode.StatusBarItem;

	constructor(id: string, name: string, alignment: vscode.StatusBarAlignment, priority: number) {
		super();
		this.f = this.c(vscode.window.createStatusBarItem(id, alignment, priority));
		this.f.name = name;
	}

	protected g(owner: unknown, text: string) {
		this.e = owner;
		this.f.text = text;
		this.f.show();
	}

	public hide(owner: unknown) {
		if (owner === this.e) {
			this.f.hide();
			this.e = undefined;
		}
	}
}
